import React from 'react';
import {Link, useParams} from "react-router-dom";
import {postLabelsList} from "../../../constants/label";
import {useSelector} from "react-redux";
import useTranslation from "../../../hooks/useTranslation";

const LabelsElement = () => {

    // manage global state
    const {fetched} = useSelector(state => state.fetchPostTypesReducer);
    const data = fetched[0].labels;

    // manage local state
    const {postType} = useParams();

    return (
        <div>
            <table className={`acpt-table acpt-table-secondary ${document.globals.is_rtl === true ? `rtl` : ``}`}>
                {postLabelsList.map((item) => (
                    <tr>
                        <th style={{width: '180px'}}>{item.label}</th>
                        <td>{data[item.id]}</td>
                    </tr>
                ))}
            </table>
            <div>
                <Link
                    className="acpt-btn acpt-btn-primary"
                    to={`/edit/${postType}/2`}
                >
                    {useTranslation("Edit")}
                </Link>
                &nbsp;
                <Link
                    className="acpt-btn acpt-btn-primary-o"
                    to={`/assoc-taxonomy-post/${postType}`}
                >
                    {useTranslation("Taxonomies association")}
                </Link>
            </div>
        </div>
    );
};

export default LabelsElement;