import React from 'react';
import {useParams} from "react-router-dom";
import {useSelector} from "react-redux";
import {Icon} from "@iconify/react";
import useTranslation from "../../../hooks/useTranslation";
import CustomPostTypeIcon from "../../reusable/CustomPostType/CustomPostTypeIcon";

const BasicElement = () => {

    // manage global state
    const {fetched} = useSelector(state => state.fetchPostTypesReducer);
    const data = {
        name: fetched[0].name,
        singular: fetched[0].singular,
        plural: fetched[0].plural,
        icon: fetched[0].icon,
        supports: fetched[0].supports
    };

    // manage local state
    const {postType} = useParams();

    return (
        <div>
            <table className={`acpt-table acpt-table-secondary ${document.globals.is_rtl === true ? `rtl` : ``}`}>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Name")}
                    </th>
                    <td>{data.name}</td>
                </tr>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Singular")}
                    </th>
                    <td>{data.singular}</td>
                </tr>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Plural")}
                    </th>
                    <td>{data.plural}</td>
                </tr>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Icon")}
                    </th>
                    <td>
                        <CustomPostTypeIcon value={data.icon} />
                    </td>
                </tr>
                <tr>
                    <th style={{width: '180px'}}>
                        {useTranslation("Supports")}
                    </th>
                    <td>
                        {data.supports && data.supports.map((s) =>
                            (s !== '') && (
                                <div className="acpt-badge mr-1">
                                    <span className="label">{s}</span>
                                </div>
                            )
                        )}
                    </td>
                </tr>
            </table>
        </div>
    );
};

export default BasicElement;